
.v-enter {
  opacity: 0;
}
.v-enter-active {
  -webkit-transition: .3s;
  transition: .3s;
}
.v-leave-to {
  opacity: 0;
}
.v-leave-active {
  -webkit-transition: .3s;
  transition: .3s;
}
html,body,#app {
  background: #fcfcfc;
  height: 100%;
  width: 100%;
  overflow-x: hidden;
}
#app {
  font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","\5FAE\8F6F\96C5\9ED1",Arial,sans-serif;
}
.router {
  margin-top: 104px;
}
a:link,a:visited,a:hover,a:active {
  text-decoration: none;
}  

.header {
  width: 100%;
  height: 64px;
  background: #d43c33;
  line-height: 64px;
  position: fixed;
  top: 0;
  z-index: 5;
}
.header p {
  font-size: 19px;
  font-weight: bold;
  color: #fff;
  display: inline-block;
  padding: 0 0 0 25px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.header .icon-yingyong {
  float: right;
  margin-right: 15px;
  color: #fff;
  font-weight: bold;
}

.router-link-active {
  /* router 内置 active  */
  color: #ef4136;
  border-bottom: 2px solid #ef4136;
}
.tab {
  width: 100%;
  height: 40px;
  background: #fff;
  border-bottom: 2px solid #ede9e9;
  position: fixed;
  top: 64px;
  z-index: 1;
}
.tab li {
  width: 25%;
  display: inline-block;
  text-align: center;
  line-height: 39px;
  font-size: 13px;
  color: #363652;
  cursor: pointer;
  list-style: none;
  font-weight: 500;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.active {
  border-bottom: 2px solid #ef4136;
  color: #d33a31;
}

#player {
    position: fixed;
    z-index: 999;
    width: 100%;
    height: 50px;
    background: rgb(247, 247, 221);
    bottom: 0;
    color: #4d4b4b;
}
.player-content {
    width: 40px;
    height: 40px;
    margin: 5px 0 0 10px;
    position: relative;
}
.song-title,.single-source {
    display: inline-block;
    position: absolute;
    top: 5px;
    left: 70px;
    font-size: 14px;
    overflow: hidden;
    text-overflow:ellipsis;
    white-space: nowrap;
    max-width: 240px;
}
.player-content img {
    width: 100%;
    height: 100%;
    -webkit-box-shadow: 0 3px 3px rgba(73, 73, 68, 0.596);
            box-shadow: 0 3px 3px rgba(73, 73, 68, 0.596);
    display: inline-block;
}
.single-author {
    margin-top: 5px;
    font-size: 13px;
}
.single-source {
    font-size: 12px;   
    top: 25px;
}
.icon-zanting,.icon-bofang {
    position: absolute;
    font-size: 40px;
    top: 50%;
    right: 5px;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
}
